import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { trendingDetails } from "../../apiServices/home/homeHttpService";

function TContentDetails() {
  let { id } = useParams();

  const { data: details } = useQuery({
    queryKey: ["contentDetail2", id],
    queryFn: async () => {
      return trendingDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.content,
  });

  return (
    <>
      {" "}
      <Header />
      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link to="/admin/content-management" className="comman_btn w-fit">
            <i className="fa fa-arrow-left" />
          </Link>

          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="user-img-wrapper mb-4">
                    <img
                      src={details?.image ? details?.image : ""}
                      alt=""
                      className="w-100 h-100"
                    />
                  </div>
                  <div className="headleft ">
                    <h2>{details?.titleEn}</h2>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">{details?.descriptionEn}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default TContentDetails;
