import { useQuery } from "@tanstack/react-query";
import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { viewProfile } from "../../apiServices/home/homeHttpService";

function Header({ update }) {
  const navigate = useNavigate();

  const { data: details } = useQuery({
    queryKey: ["admin", update],
    queryFn: viewProfile,
    select: (data) => data.results,

    onError: (error) => {
      alert.error(error.message);
    },
  });

  const logout = (e) => {
    e.preventDefault();
    if (localStorage.getItem("token-timp")) {
      localStorage.removeItem("token-timp");
    }
    navigate("/admin/login");
  };
  return (
    <>
      <div className="admin_header">
        <div className="row justify-content-between align-items-center">
          <div className="col-2 text-start">
            <Link className="logo" to="/admin/dashboard">
              <img src="../../assets/img/logo.png" alt="" />
            </Link>
          </div>
          <div className="col-auto">
            <div className="row">
              {/* <div class="col-auto pe-2">
             <a class="language_change" href="javascript:;"><img src="../../assets/img/arabic.png" alt="">Arabic</a>
          </div> */}
              <div className="col-auto d-flex align-items-center">
                <div className="dropdown Profile_dropdown">
                  <button
                    className="btn btn-secondary"
                    type="button"
                    id="dropdownMenuButton1"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    <img
                      src={
                        details?.image
                          ? details?.image
                          : "../../assets/img/user1.png"
                      }
                      alt=""
                    />
                  </button>
                  <ul
                    className="dropdown-menu"
                    aria-labelledby="dropdownMenuButton1"
                  >
                    <li>
                      <Link className="dropdown-item" to="/admin/view-profile">
                        View Profile
                      </Link>
                    </li>
                    <li>
                      <Link
                        className="dropdown-item"
                        to="/admin/change-password"
                      >
                        Change Password
                      </Link>
                    </li>
                    <li>
                      <Link
                        className="dropdown-item"
                        to=""
                        onClick={(e) => logout(e)}
                      >
                        Logout
                      </Link>
                    </li>
                  </ul>
                </div>
                <div className="toggle_btns">
                  <i className="fas fa-bars" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Header;
