import webHttpService from "../webHttpService";

export async function login(formData) {
  try {
    const { data } = await webHttpService.put(
      `${process.env.REACT_APP_APIENDPOINT}/admin/login`,
      formData
    );
    console.log(data);

    return data;
  } catch (error) {
    alert(error.message);
  }
}

