import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useAlert } from "react-alert";
import { useQuery } from "@tanstack/react-query";
import { merchantRedflagDetails } from "../../apiServices/home/homeHttpService";
import moment from "moment";

function RedflagDetailsMerchant() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();
  const alert = useAlert();

  const { data: details, refetch } = useQuery({
    queryKey: ["merchantRedFlagListDetail", id],
    queryFn: async () => {
      return merchantRedflagDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.merchant,
  });

  // const changeStatus = async (v) => {
  //   const formData = {
  //     status: v,
  //   };
  //   const response = await UserUpdateStatus(id, formData);
  //   if (!response.error) {
  //     showAlert(alert, response.message, { timeout: 3000 });
  //     refetch();
  //   } else {
  //     showAlert(alert, response.message, { timeout: 3000 });
  //   }
  // };

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link
            to={`/admin/red-flag-management?tab=${
              queryParams.get("tab") || 1
            }&page=${queryParams.get("page") || 1}`}
            className="comman_btn w-fit"
          >
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-12 mx-auto">
                  <div>
                    <div className="d-flex ">
                      <div className="user-img-wrapper">
                        <img
                          src={
                            details?.restaurant?.merchantId?.profile
                              ? details?.restaurant?.merchantId?.profile
                              : "../../assets/img/user1.png"
                          }
                          alt=""
                          className="w-100 h-100"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-12 col-md-8 col-12 order-md-0 order-1">
                    <div className="mt-4 user-view-wrapper">
                      <div className="mt-2">
                        <strong>Name</strong>
                        <span>{details?.restaurant?.merchantId?.name}</span>
                      </div>
                      <div className="mt-2">
                        <strong>Email</strong>
                        <span>{details?.restaurant?.merchantId?.email}</span>
                      </div>
                      <div className="mt-2">
                        <strong>Mobile No.</strong>
                        <span>
                          {details?.restaurant?.merchantId?.mobileNumber}
                        </span>
                      </div>
                      <div className="mt-2">
                        <strong>Status </strong>

                        <span
                          className={
                            details?.restaurant?.merchantId?.status
                              ? "status_accepted"
                              : "status_declained"
                          }
                        >
                          {details?.restaurant?.merchantId?.status
                            ? "Active"
                            : "Inactive"}
                        </span>
                      </div>
                      <div className="mt-2">
                        <strong>Registered At</strong>
                        <span>
                          {moment(details?.createdAt).format(
                            "DD MMM YYYY, hh:mm A"
                          )}
                        </span>
                      </div>

                      <div className="ps-2">
                        <div className="mt-3">
                          <p className="fw-semibold fs-5 border-bottom w-100 pb-2 border-2">
                            Red Flag Content
                          </p>
                          <p className="w-100 h6 fw-light">
                            {details?.description}
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default RedflagDetailsMerchant;
