import React from "react";
import { Link, useNavigate } from "react-router-dom";

function SideBar() {
  const navigate = useNavigate();

  const logout = (e) => {
    e.preventDefault();
    if (localStorage.getItem("token-timp")) {
      localStorage.removeItem("token-timp");
    }
    navigate("/admin/login");
  };
  return (
    <>
      <div className="admin_siderbarr">
        <div className="overlay_show" />
        <Link
          className={
            window.location.href.includes("/admin/dashboard")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/dashboard"
        >
          <span>
            <i className="fas fa-home" />
          </span>
          Dashboard
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/users-management") ||
            window.location.href.includes("/admin/user-details")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/users-management"
        >
          <span>
            <i className="fas fa-users" />
          </span>
          Users Management
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/merchant-management") ||
            window.location.href.includes("/admin/merchant-details") ||
            window.location.href.includes("/admin/add-merchant")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/merchant-management"
        >
          <span>
            <i className="fas fa-hotel" />
          </span>
          Merchant Management
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/rating-and-review-management")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/rating-and-review-management"
        >
          <span>
            <i className="fas fa-stars" />
          </span>
          Rating and Review
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/deal-management") |
              window.location.href.includes("/admin/add-deal") ||
            window.location.href.includes("/admin/add-coupon")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/deal-management"
        >
          <span>
            <i className="fas fa-handshake" />
          </span>
          Deal Management
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/subscription-plan") ||
            window.location.href.includes("/admin/add-subscription-plan") ||
            window.location.href.includes("/admin/subscription-plan-details")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/subscription-plan"
        >
          <span>
            <i className="fas fa-hand-holding-usd" />
          </span>
          Subscription Plan
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/advertisement-package")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/advertisement-package"
        >
          <span>
            <i className="fas fa-user" />
          </span>
          Advertisement Package
        </Link>
        <Link
          className={
            window.location.href.includes("/admin/promotional-banner")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/promotional-banner"
        >
          <span>
            <i className="fas fa-bullhorn" />
          </span>
          Promotional Banner
        </Link>
        <Link
          className={
            window.location.href.includes("flag")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/red-flag-management"
        >
          <span>
            <i className="fas fa-flag" />
          </span>
          Red Flag Content
        </Link>
        <Link
          className={
            window.location.href.includes("transaction")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/transaction-management"
        >
          <span>
            <i className="fas fa-sync-alt" />
          </span>
          Transaction Management
        </Link>
        <Link
          className={
            window.location.href.includes("maintenance")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/maintenance-and-updates"
        >
          <span>
            <i className="fas fa-tools" />
          </span>
          Maintenance and Updates
        </Link>
        <Link
          className={
            window.location.href.includes("help")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/help-&-Support"
        >
          <span>
            <i className="fas fa-headset" />
          </span>
          Help &amp; Support
        </Link>
        <Link
          className={
            window.location.href.includes("content")
              ? "active sidebar-link disabled"
              : "sidebar-link"
          }
          to="/admin/content-management"
        >
          <span>
            <i className="fas fa-edit" />
          </span>
          Content Management
        </Link>
        <Link className="sidebar-link" to="" onClick={(e) => logout(e)}>
          <span>
            <i className="fas fa-sign-out-alt" />
          </span>
          Logout
        </Link>
      </div>
    </>
  );
}

export default SideBar;
